import React, { useState, useEffect } from 'react';
import { useHistory } from "react-router-dom";
import { Row, Col, Container, Button, Input, FormGroup, Label, Form, UncontrolledDropdown, DropdownToggle, DropdownMenu, DropdownItem } from 'reactstrap';
const NewRule = (props) => {

    const history = useHistory();
    const [error, setError] = useState(null)
    const [loading, setIsLoading] = useState(true)
    const [selectedMethod, setSelectedMethod] = useState(null)
    const [RBACRoles, setRBACRoles] = useState([])
    const [otherMeth, setOtherMeth] = useState([])
    const [SPName, setSPName] = useState("")
    const [RBACCustomRole, setRBACCustomRole] = useState('')
    const [sampleRBACRoles,] = useState(['Marketing', 'Finance', 'Programmer', 'Support'])
    const [, updateState] = React.useState();
    const forceUpdate = React.useCallback(() => updateState({}), []);

    const addRBACRole = (added_role) => {
        let updatedRoleList = RBACRoles
        updatedRoleList.push(added_role)
        setRBACRoles(updatedRoleList)
        forceUpdate()
    }

    const toggleOthersMeth = (added_meth) => {
        let otherMethList = otherMeth
        if (otherMethList.includes(added_meth)) {
            let index = otherMethList.indexOf(added_meth)
            otherMethList.splice(index, 1);
        } else {
            otherMethList.push(added_meth)
        }
        setOtherMeth(otherMethList)
        forceUpdate()
    }

    const removeRBACRole = (index) => {
        let updatedRoleList = RBACRoles
        updatedRoleList.splice(index, 1);
        setRBACRoles(updatedRoleList)
        if (updatedRoleList.length === 0) {
            setOtherMeth([])
        }
        forceUpdate()
    }


    const addRBACCustomRole = () => {
        let newRole = RBACCustomRole
        if (newRole.replaceAll(' ', '') === '') {
            return
        } else {
            let updatedRoleList = RBACRoles
            updatedRoleList.push(newRole)
            setRBACRoles(updatedRoleList)
            setRBACCustomRole('')
            forceUpdate()
        }
    }




    const onAddRBACRule = () => {
        if (RBACRoles.length === 0) {
            return
        }
        let url = ''
        let body = null
        switch (selectedMethod) {
            case 'RBAC':
                url = `/pdpa/admin/addRBACRule`
                body = {'name':SPName, 'roles': RBACRoles, 'others': otherMeth, 'modified_time': Math.floor(Date.now()) }
        }

        fetch(url, {
            method: 'POST',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify(body)
        })
            .then((res) => res.json())
            .then((data) => {
                if (data['err']) {
                    alert(data['err'])
                    return
                }
                // console.log(data)
                alert('The rule is successfully added')
                history.push("/pdpa/auth/security-policy")

                // window.location.reload()

            }).catch((err) => alert('Unexpected error occurred, Please try again later'))
    }

    const changeRBACCustomRole = (e) => { setRBACCustomRole(e.target.value); }
    const changeSPName = (e) => { setSPName(e.target.value); }

    // console.log('sampleRBACRoles', sampleRBACRoles)
    return (
        <Container fluid style={{ minHeight: '86vh', width: '100vw', justifyContent: 'center' }}>
            <header className='pt-2'>
                <Container className='pt-2' style={{ width: "max(50%,320px)" }}>
                    <Container className='pt-1 pb-4' sm='10' md='8' lg='6' xl='6' >
                        <Row className='pt-4 pb-4' style={{ paddingLeft: '5%', paddingRight: '5%' }}>
                            {/* <h2 style={{ textAlign: 'center' }}>Create New Rule</h2> */}
                            <h3 style={{ textAlign: 'center', fontWeight: 550 }}>CREATE NEW RULE</h3>
                        </Row>
                    </Container>
                </Container>
            </header>
            <Container style={{ width: "max(50%,320px)" }}>
                <Container className='pt-1 pb-4' sm='10' md='8' lg='6' xl='6' >
                    <Row className='pt-1 pb-3' style={{ width: '100%', justifyContent: 'center', margin: 0 }}>
                        <Col className='mb-3' xs='12' sm='12' md='10' lg='10' xl='10' >

                            <div className='mb-3'>
                                <h5 className='mb-2'>Policy Name:</h5>
                                <Input type="text" name="text" id="name" placeholder="name" onChange={changeSPName} value={SPName} />
                            </div>
                        </Col>
                        <Col className='mb-3' xs='12' sm='12' md='10' lg='10' xl='10' >
                            <h5 className='mb-2'>Select Access Control Method:</h5>
                            <div>
                                <p><input disabled type="radio" checked={selectedMethod == "MAC"} onClick={() => { setSelectedMethod("MAC"); }} /> {'Mandatory Access Control (MAC)'}</p>
                                <p><input type="radio" checked={selectedMethod == "RBAC"} onClick={() => { setSelectedMethod("RBAC"); }} /> {'Role-based Access Control (RBAC)'}</p>
                                <p><input disabled type="radio" checked={selectedMethod == "ABAC"} onClick={() => { setSelectedMethod("ABAC"); }} /> {'Attribute-based Access Control (ABAC)'}</p>
                            </div>
                        </Col>
                        <Col xs='12' sm='12' md='10' lg='10' xl='10' >
                            {(selectedMethod === 'RBAC') && (
                                <div className='mt-3'>
                                    <h5 className='mb-3'>Select Roles:</h5>
                                    {/* <Col xs='11' sm='11' md='10' lg='10' xl='10' > */}
                                    {/* <Input size='xs' style={{ width: '80%' }}  placeholder='Email Address'  /> */}
                                    <Row>
                                        <Col xs='4' sm='4' md='4' lg='4' xl='4' style={{ display: 'flex', alignItems: "center" }}>
                                            <UncontrolledDropdown>
                                                <DropdownToggle caret>
                                                    Select
                                                </DropdownToggle>
                                                <DropdownMenu>
                                                    {sampleRBACRoles.map((el) => (
                                                        <DropdownItem onClick={() => { addRBACRole(el); }}>{el}</DropdownItem>
                                                    ))}
                                                </DropdownMenu>
                                            </UncontrolledDropdown>
                                        </Col>
                                        <Col xs='8' sm='8' md='8' lg='8' xl='8' style={{ display: 'flex', alignItems: "center" }}>
                                            <div style={{ display: 'flex', alignItems: "center" }}>
                                                <div style={{ display: 'inline-block', width: '80%' }}>
                                                    <Input type="text" name="text" id="exampleEmail" placeholder="other role" onChange={changeRBACCustomRole} value={RBACCustomRole} />
                                                </div>
                                                <div style={{ display: 'inline-block', width: '20%' }}>
                                                    <Button style={{ boxShadow: 'none', display: 'inline', backgroundColor: 'transparent', borderColor: 'transparent', color: "black" }} onClick={() => addRBACCustomRole()}>
                                                        <span class="material-symbols-outlined" style={{ fontSize: '1.87em' }}>
                                                            add_circle
                                                        </span>
                                                    </Button>
                                                </div>
                                            </div>
                                        </Col>
                                    </Row>
                                    <Row className='mt-2' style={{ paddingLeft: '3%', overflowY: 'scroll', maxHeight: '20vh' }}>
                                        {(RBACRoles.length > 0) && (RBACRoles.map((role, i) => (
                                            <div style={{ display: 'flex', alignItems: "center" }}>
                                                <div style={{ display: 'inline-block', width: '75%' }}>
                                                    <h6>{role}</h6>
                                                </div>
                                                <div style={{ display: 'inline-block', width: '25%', height: 'auto' }}>
                                                    <Button onClick={() => removeRBACRole(i)} style={{ boxShadow: 'none', display: 'inline', backgroundColor: 'transparent', borderColor: 'transparent', color: "black" }}>
                                                        <span class="material-symbols-outlined" style={{ fontSize: '1.2em', color: 'red' }}>
                                                            close
                                                        </span>
                                                    </Button>
                                                </div>
                                            </div>
                                        )))}

                                    </Row>
                                    {(RBACRoles.length > 0) && (
                                        <Row className='mt-4'>
                                            <Col xs='12' sm='12' md='10' lg='10' xl='10'>
                                                <h5 className='mb-2'>Others:</h5>
                                                <div style={{ paddingLeft: '3%' }}>
                                                    <div style={{ display: 'flex', alignItems: 'center' }}><p><input type="checkbox" checked={otherMeth.includes('ENCRYPTION')} onClick={() => { toggleOthersMeth('ENCRYPTION'); }} /> {' Encryption'}</p></div>
                                                    <div style={{ display: 'flex', alignItems: 'center' }}><p><input type="checkbox" checked={otherMeth.includes('READ-ONLY')} onClick={() => { toggleOthersMeth('READ-ONLY'); }} /> {' Read-only'}</p></div>
                                                    <div style={{ display: 'flex', alignItems: 'center' }}><p><input type="checkbox" checked={otherMeth.includes('BACKUP')} onClick={() => { toggleOthersMeth('BACKUP'); }} /> {' Backup'}</p></div>
                                                    {/* <input type="radio" checked={selectedMethod == "RBAC"} onClick={() => { setSelectedMethod("RBAC"); }} /> {'Role-based Access Control (RBAC)'}<br />
                                                <input type="radio" checked={selectedMethod == "ABAC"} onClick={() => { setSelectedMethod("ABAC"); }} /> {'Attribute-baesd Access Control (ABAC)'}<br /> */}
                                                </div>
                                            </Col>
                                        </Row>

                                    )}
                                </div>
                            )}
                        </Col>
                    </Row>
                    {(RBACRoles.length > 0) && (
                        <Row className='pt-4' style={{ width: '100%', justifyContent: 'center', margin: 0 }}>
                            <Col xs='11' sm='11' md='10' lg='10' xl='10' >
                            <Button size='lg' style={{ width: '100%', backgroundColor:"#3c6970" }} onClick={() => onAddRBACRule()}><h6>SUBMIT</h6></Button>
                            </Col>
                        </Row>)}
                    {/* <Row className='pt-1 pb-3' style={{ width: '100%', justifyContent: 'center', margin: 0 }}>
            <Col xs='12' sm='12' md='10' lg='10' xl='10' >
              <Button outline size='lg' style={{ width: '100%',  height:'max(12vh,65px)'}}>
                <Row>
                  <Col xs='9' sm='9' md='9' lg='9' xl='9' style={{display:'flex', justifyContent:'cent', alignItems:'center'}}>
                    <h4 style={{ textAlign: 'left', paddingLeft: '8%' }}>Consent Template</h4>
                  </Col>
                  <Col xs='3' sm='3' md='3' lg='3' xl='3'>
                    <span class="material-symbols-outlined" style={{fontSize:'1.9em'}}>
                      edit_document
                    </span>
                  </Col>
                </Row>
              </Button>
            </Col>
          </Row> */}
                    {/* <Row className='pt-1 pb-3' style={{ width: '100%', justifyContent: 'center', margin: 0 }}>
            <Col xs='12' sm='12' md='10' lg='10' xl='10' >
              <Button outline size='lg' style={{ width: '100%', height:'max(12vh,65px)'}}>
                <Row>
                  <Col xs='9' sm='9' md='9' lg='9' xl='9' style={{display:'flex', justifyContent:'cent', alignItems:'center'}}>
                    <h4 style={{ textAlign: 'left', paddingLeft: '8%' }}>Application Consent</h4>
                  </Col>
                  <Col xs='3' sm='3' md='3' lg='3' xl='3'>
                    <span class="material-symbols-outlined" style={{fontSize:'1.9em'}}>
                      widgets
                    </span>
                  </Col>
                </Row>
              </Button>
            </Col>
          </Row> */}
                </Container>
            </Container>
        </Container>
    );
}

export default NewRule