import React, { Component } from 'react'
import { Spinner } from 'reactstrap';


export default class extends Component {
    constructor(props) {
        super(props)
    }

    render() {
        return (
            <div className="App" style={{ minHeight: '86vh', display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
                <div className='mb-5'>
                    {/* <img src="https://cdn.jsdelivr.net/....." className="App-logo-loader" alt="logo" /> */}
                    <div className='mt-4 mb-3'>
                        <Spinner size='lg' style={{ color: 'rgba(5,104,57,0.5)' }} type="grow" />
                    </div>
                    <h4 className='mb-3' style={{ color: '#676D75', fontWeight:"550" }}>Loading Data ...</h4>
                </div>
            </div>
        )
    }
}
