import React, { useState, useEffect } from 'react';
import { Row, Col, Container, Button } from 'reactstrap';
import { Input } from 'semantic-ui-react'
import Loader from '../../../Components/Loader';
import { Link } from "react-router-dom";
const ViewAllAppConsent = (props) => {

  // const [data, setData] = useState([{}])
  const [error, setError] = useState(null)
  const [loading, setIsLoading] = useState(true)
  const [applicationData, setAppData] = useState({})
  const [category_icon,] = useState({
    'Entertainment' : 'sports_esports',
    'Delivery Service':'local_shipping',
    'Shopping': 'shopping_bag',
    'Food and Drinks': 'restaurant'
  })

  useEffect(() => {
    // console.log(props.match.params.uid, 'uid is here')
    const abortCont = new AbortController();
    fetch("/pdpa/admin/getApplicationData", { signal: abortCont.signal }).then(
      res => res.json()
    ).then(
      data => {
        // console.log(data.data, 'data from fb')
        setAppData(data.data)
        setIsLoading(false)
        window.scrollTo(0, 0);
      }
    ).catch(err => {
      if (err.name === 'AbortError') {
        console.log('fetch aborted')
      } else {
        setIsLoading(false);
        setError(err.message);

      }
    })
    return () => abortCont.abort();
  }, [])

  const delApp = (docID) => {
    // console.log(docID, 'del docID')
    let confirmDel = window.confirm('Do you confirm to delete this item?');
    if (confirmDel) {
      let body = { 'docID': docID }
      fetch(`/pdpa/admin/deleteApp`, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(body)
      })
        .then((res) => res.json())
        .then((data) => {
          if (data['err']) {
            alert(data['err'])
            return
          }
          window.location.reload()
        }).catch((err) => alert('Unexpected error occurred, Please try again later'))
    } else {
      return
    }
  }

  let applicationDataUI = []
  if (applicationData !== {}) {
    let applicationDataDocKeys = Object.keys(applicationData)
    // console.log(applicationData, 'applicationData')
    // let category_icon = null
    let application = null
    // console.log(applicationDataDocKeys, 'applicationDataDocKeys')
    for (let k in applicationDataDocKeys) {
      application = applicationData[applicationDataDocKeys[k]]
      applicationDataUI.push(
        <Col className='pt-1 pb-2' xs='12' sm='12' md='10' lg='10' xl='10' key={k}>
          <Button outline size='lg' style={{ width: '100%', height: 'auto', minHeight: '12vh', paddingRight: "1%" }}>
            <Row>
              <Col xs='3' sm='3' md='3' lg='3' xl='3' style={{ display: 'flex', alignItems: "center" }}>
                <span class="material-symbols-outlined" style={{width:'100%', fontSize:"2em"}}>
                  {category_icon[application.category]}
                </span>
              </Col>
              <Col xs='7' sm='7' md='7' lg='7' xl='7' style={{ textAlign: 'left' }}>
                <h6 style={{ fontWeight: 600 }}>{application.name}</h6>
                <span style={{ fontSize: '0.78em' }}><i>{application.category}</i></span>
              </Col>

              <Col xs='1' sm='1' md='1' lg='1' xl='1' style={{ display: "flex", justifyContent: 'center', alignItems: 'center' }} >
                <span class="material-symbols-outlined" onClick={() => delApp(applicationDataDocKeys[k])} style={{ fontSize: "1.3em" }}>
                  delete
                </span>
              </Col>
            </Row>
          </Button>
        </Col>
      )
    }
  }

  if(loading){
    return(<Loader/>)
  }

  return (
    <Container fluid style={{ minHeight: '86vh', width: '100vw', justifyContent: 'center' }}>
      <header className='pt-2'>
        <Container className='pt-2' style={{ width: "max(50%,320px)" }}>
          <Container className='pt-1 pb-4' sm='10' md='8' lg='6' xl='6' >
            <Row className='pt-4 pb-4' style={{ paddingLeft: '5%', paddingRight: '5%' }}>
              <h2 style={{ textAlign: 'center' }}>Application Consent</h2>
            </Row>
          </Container>
        </Container>
      </header>
      <Container style={{ width: "max(50%,320px)" }}>
        <Container className='pt-1 pb-4' sm='10' md='8' lg='6' xl='6' >
          <Row className='pt-1 pb-3' style={{ width: '100%', justifyContent: 'center', margin: 0 }}>
            <Col xs='12' sm='12' md='10' lg='10' xl='10' >
              <Link to={"/pdpa/auth/app-consent/new"}>
                <Button className='shadow' color='secondary' size='lg' style={{ width: '100%', height: 'max(12vh,65px)', backgroundColor: '#315589', borderColor: "transparent" }}>
                  <Row>
                    <Col xs='9' sm='9' md='9' lg='9' xl='9' style={{ display: 'flex', justifyContent: 'cent', alignItems: 'center' }}>
                      <h4 style={{ textAlign: 'left', paddingLeft: '8%' }}>New Application</h4>
                    </Col>
                    <Col xs='3' sm='3' md='3' lg='3' xl='3'>
                      <span class="material-symbols-outlined" style={{ fontSize: '1.9em' }}>
                        add_circle
                      </span>
                    </Col>
                  </Row>
                </Button>
              </Link>
            </Col>
          </Row>
          <Row className='pt-1 pb-3' style={{ width: '100%', justifyContent: 'center', margin: 0 }}>
            {applicationDataUI}
          </Row>
        </Container>
      </Container>
    </Container>
  );
}

export default ViewAllAppConsent