import { Row, Col, Container, Button } from 'reactstrap';
import { Link } from "react-router-dom";
import { Input } from 'semantic-ui-react'
import React, { useState } from 'react';
import Loader from '../Components/Loader';

const Register = (props) => {
    const [email, setEmail] = useState('');
    const [passwd, setPasswd] = useState('');
    const [confPass, setConfPass] = useState('')
    const [isAccCreated, setAccCreated] = useState(false);
    const [errMessage, setErrMessage] = useState('')
    const [loading, setIsLoading] = useState(false)

    const changeEmail = (e) => { setEmail(e.target.value); }
    const changePasswd = (e) => { setPasswd(e.target.value); }
    const changeConfPasswd = (e) => { setConfPass(e.target.value); }

    const register = () => {
        setIsLoading(true)
        if (email === "" || passwd === "" || confPass === "") {
            alert('Please complete all required fields')
            return
        }
        if (passwd !== confPass) {
            alert('Passwords are not matched')
            return
        }
        let body = { 'email': email, 'password': passwd }
        fetch(`/pdpa/admin/registerUser`, {
            method: 'POST',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify(body)
        })
            .then((res) => res.json())
            .then((data) => {
                if (data['err']) {
                    alert(data['err'])
                    return
                }
                setIsLoading(false)
                setAccCreated(true)

            }).catch((err) => {alert('Unexpected error occurred, Please try again later');setIsLoading(false)})
    }

    if(loading){
        return(<Loader/>)
      }

    return (
        <Container fluid style={{ height: '100vh', width:'100vw', justifyContent: 'center' }}>
            <header className='pt-1'>
                <Container className='pt-4' style={{  minWidth: '300px', maxWidth:"50%" }}>
                    <Container className='pt-4 pb-4' sm='10' md='8' lg='6' xl='6' >
                        <Row className='pt-4 pb-2' style={{ paddingLeft: '1%', paddingRight: '1%' }}>
                            <h3 style={{ textAlign: 'center', fontWeight:'600', color:'#5c717b'}}>PDPA Tool for Data Controller</h3>
                        </Row>
                    </Container>
                </Container>
            </header>
            <Container className='pt-0 shadow' style={{  minWidth: '300px', maxWidth:"50%", borderRadius:'15px',backgroundColor:'#f6f6f6' }}>
                <Container className='pt-3 pb-0' sm='10' md='8' lg='6' xl='6' >
                    <Row className='pt-1' style={{ paddingLeft: '5%', paddingRight: '5%' }}>
                        <Col style={{ textAlign: 'center' }}>
                            <Link to={"/pdpa/login"} style={{textDecoration:'none'}}>
                                <Button outline style={{ width: '75%', borderColor: 'transparent',boxShadow: 'none', display: 'inline', backgroundColor: 'transparent', borderColor: 'transparent',color: "black" }}><p style={{ textAlign: 'center' }}>Login</p></Button>
                            </Link>
                        </Col>
                        <Col style={{ textAlign: 'center' }}>
                            <Link to={"/pdpa/register"} style={{textDecoration:'none'}}>
                                <Button outline style={{ width: '75%', borderColor: 'transparent',boxShadow: 'none', display: 'inline', backgroundColor: 'transparent', borderColor: 'transparent',color: "black"}}><p style={{ textAlign: 'center' }}>Register</p></Button>
                            </Link>
                        </Col>
                    </Row>
                    <hr />
                </Container>

                <Container className='pt-0 pb-4' sm='10' md='8' lg='6' xl='6' >
                    <Row className='pt-1 pb-4'>
                        <h4 style={{ textAlign: 'center' }}>REGISTER</h4>
                    </Row>
                    {(isAccCreated) && (
                        <>
                        <Row className='pt-4' style={{ width: '100%', justifyContent: 'center', margin: 0 }}>
                                <Col className='pb-4' xs='11' sm='11' md='10' lg='10' xl='10' >
                                <h5 style={{ textAlign: 'center' }}>Your account is successfully created.<br/> You can now login to enter the system</h5>
                                </Col>
                            </Row>
                        </>
                    )}

                    {(!isAccCreated) && (
                        <>
                            <Row className='pt-1' style={{ width: '100%', justifyContent: 'center', margin: 0 }}>
                                <Col xs='12' sm='11' md='10' lg='10' xl='10' >
                                    <Input style={{ width: '100%', fontSize:"1em" }} icon='mail' iconPosition='left' placeholder='Email Address' onChange={changeEmail} value={email} />
                                </Col>
                            </Row>
                            <Row className='pt-1' style={{ width: '100%', justifyContent: 'center', margin: 0 }}>
                                <Col xs='12' sm='11' md='10' lg='10' xl='10' >
                                    <Input style={{ width: '100%', fontSize:"1em" }} icon='key' type='password' iconPosition='left' placeholder='Password' onChange={changePasswd} value={passwd} />
                                </Col>
                            </Row>
                            <Row className='pt-1' style={{ width: '100%', justifyContent: 'center', margin: 0 }}>
                                <Col xs='12' sm='11' md='10' lg='10' xl='10' >
                                    <Input style={{ width: '100%', fontSize:"1em" }} icon='key' type='password' iconPosition='left' placeholder='Confirmed Password' onChange={changeConfPasswd} value={confPass} />
                                </Col>
                            </Row>
                            <Row className='pt-4 pb-4' style={{ width: '100%', justifyContent: 'center', margin: 0 }}>
                                <Col xs='12' sm='11' md='10' lg='10' xl='10' >
                                    <Button style={{ width: '100%', fontSize:"1em", backgroundColor:'#a15255', fontWeight:600, borderColor:"transparent"  }} onClick={() => register()}>SIGN UP</Button>
                                </Col>
                            </Row>
                        </>
                    )}


                </Container>
            </Container>


            {/* <button onClick={(e)=>changeEmail(e,email)}>Change Personal State</button> */}

        </Container>
    );
}

export default Register;
