import React, { useState, useEffect, useRef } from 'react';
import { useHistory } from "react-router-dom";
import Loader from '../../../Components/Loader';
import {
    Row, Col, Container, Button,
    Modal, ModalHeader, ModalBody, ModalFooter,
    Input, FormGroup, Label, Form, UncontrolledDropdown, DropdownToggle, DropdownMenu, DropdownItem
} from 'reactstrap';
const NewAppConsent = (props) => {
    const inputRef = useRef(null);
    const history = useHistory();
    const [error, setError] = useState(null)
    const [loading, setIsLoading] = useState(true)
    const [selectCategory, setSelectCategory] = useState(null)
    const [sampleCategory,] = useState(['Entertainment', 'Delivery Service', 'Shopping', 'Food and Drinks'])
    const [, updateState] = React.useState();
    const [appName, setAppName] = useState('')
    const [consentTemplates, setCT] = useState([])
    const [selectedCT, setSelectedCT] = useState(null)
    const [disabledPage, setDisabledPage] = useState(false)

    useEffect(() => {
        // console.log(props.match.params.uid, 'uid is here')
        const abortCont = new AbortController();
        fetch("/pdpa/admin/getConsentTemplate", { signal: abortCont.signal }).then(
            res => res.json()
        ).then(
            data => {

                let docKeys = Object.keys(data.data)
                let arrayOfDataconsentTemplates = Object.values(data.data)
                let formattedDataconsentTemplatesArray = []
                arrayOfDataconsentTemplates.map((item, index) => {
                    formattedDataconsentTemplatesArray.push({ ...item, 'docID': docKeys[index] })
                })
                if (formattedDataconsentTemplatesArray.length === 0) {
                    setDisabledPage(true)
                }
                // console.log(formattedDataconsentTemplatesArray, 'data11')
                setCT(formattedDataconsentTemplatesArray)
                // console.log(formattedDataconsentTemplatesArray, 'formattedDataconsentTemplatesArray')
                setIsLoading(false)
                window.scrollTo(0, 0);
            }
        ).catch(err => {
            if (err.name === 'AbortError') {
                console.log('fetch aborted')
            } else {
                setIsLoading(false);
                setError(err.message);

            }
        })
        return () => abortCont.abort();
    }, [])

    const forceUpdate = React.useCallback(() => updateState({}), []);

    const onAddApplication = () => {
        if (selectedCT === null || selectCategory === null || appName.replaceAll(' ', '') === '') {
            alert('Please fill data in all fields')
            return
        }
        let requestPayload = {}
        requestPayload['consentTemplate'] = selectedCT['docID']
        requestPayload['category'] = selectCategory
        requestPayload['name'] = appName
        requestPayload['modified_time'] = Math.floor(Date.now())

        let url = '/pdpa/admin/createNewApplication'
        let body = requestPayload
        // console.log(body, 'BD')

        fetch(url, {
            method: 'POST',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify(body)
        })
            .then((res) => res.json())
            .then((data) => {
                if (data['err']) {
                    alert(data['err'])
                    return
                }
                alert('Successfully Added')
                history.push("/pdpa/auth/app-consent")

                // window.location.reload()

            }).catch((err) => alert('Unexpected error occurred, Please try again later'))
    }

    const onSelectCategory = (category) => { setSelectCategory(category); forceUpdate(); }
    const onSelectCT = (val) => { setSelectedCT(val); forceUpdate(); }

    const removeSelectCategory = () => { setSelectCategory(null); forceUpdate(); }
    const removeSelectCT = () => { setSelectedCT(null); forceUpdate(); }

    const changeAppName = (e) => { setAppName(e.target.value); }

    if (loading) {
        return (<Loader />)
    }

    return (
        <Container fluid style={{ minHeight: '86vh', width: '100vw', justifyContent: 'center' }}>
            <header className='pt-2'>
                <Container className='pt-2' style={{ width: "max(50%,320px)" }}>
                    <Container className='pt-1 pb-4' sm='10' md='8' lg='6' xl='6' >
                        <Row className='pt-4 pb-4' style={{ paddingLeft: '5%', paddingRight: '5%' }}>
                            <h2 style={{ textAlign: 'center' }}>Application Consent</h2>
                        </Row>
                    </Container>
                </Container>
            </header>
            {(disabledPage) && (
                <Container style={{ width: "max(50%,320px)" }}>
                    <Container className='pt-4 pb-4' sm='10' md='8' lg='6' xl='6' >
                        <h4 style={{ textAlign: 'center', color: "#a15255" }}>Please create a consent template before add new application consent</h4>
                    </Container>
                </Container>
            )}
            {(!disabledPage) && (
                <Container style={{ width: "max(50%,320px)" }}>
                    <Container className='pt-0 pb-4' xs='11' sm='11' md='8' lg='8' xl='8' >
                        <Row className='pt-0 pb-3' style={{ width: '100%', justifyContent: 'center', margin: 0 }}>
                            <Col className='mt-0' xs='12' sm='12' md='11' lg='11' xl='11' >
                                <div className='mb-4' style={{ display: 'flex', alignItems: "center" }}>
                                    <div style={{ display: 'inline-block', width: '100%' }}>
                                        <h5 className='mb-2'>Application Name:</h5>
                                        <Input type="text" name="text" id="exampleEmail" onChange={changeAppName} placeholder="Application name" value={appName} />
                                    </div>
                                    {/* <Row>
                                    <Col>
                                        <Button disabled={(dataItems.length === 0 || templateName.replaceAll(' ', '') === '') ? true : false} onClick={() => onCreateTemplate()} color='primary' style={{ width: '100%', boxShadow: 'none', display: 'inline', display: 'flex', justifyContent: "center", alignItems: 'center' }}>
                                            <h6 style={{ margin: 0, marginRight: '3%' }}>Create</h6>
                                        </Button>
                                    </Col>
                                </Row> */}
                                </div>
                                <div className='pt-1'>
                                    <Row>
                                        <Col xs='5' sm='5' md='5' lg='5' xl='5' style={{ display: 'flex', alignItems: 'center' }}>
                                            <Label style={{ margin: 0 }}>
                                                <h6>
                                                    Category :
                                                </h6>
                                            </Label>
                                        </Col>
                                        <Col xs='7' sm='7' md='7' lg='7' xl='7'>
                                            {(selectCategory === null) && (
                                                <UncontrolledDropdown>
                                                    <DropdownToggle caret outline style={{ width: '100%' }}>
                                                        Select Category
                                                    </DropdownToggle>
                                                    <DropdownMenu style={{ width: '100%' }}>
                                                        {sampleCategory.map((el) => (
                                                            <DropdownItem onClick={() => { onSelectCategory(el); }}>{el}</DropdownItem>
                                                        ))}
                                                    </DropdownMenu>
                                                </UncontrolledDropdown>
                                            )}
                                            {(selectCategory !== null) && (
                                                <div style={{ display: 'flex', alignItems: "center" }}>
                                                    <div style={{ display: 'inline-block', width: '80%' }}>
                                                        <label><p>{selectCategory}</p></label>
                                                    </div>
                                                    <div style={{ display: 'inline-block', width: '20%' }}>
                                                        <Button onClick={() => removeSelectCategory()} style={{ boxShadow: 'none', backgroundColor: 'transparent', borderColor: 'transparent', display: 'flex', alignItems: 'center' }}>
                                                            <span class="material-symbols-outlined" style={{ fontSize: '1.2em', color: 'red' }}>
                                                                close
                                                            </span>
                                                        </Button>
                                                    </div>
                                                </div>
                                            )}
                                        </Col>
                                    </Row>
                                    <Row className='mt-2'>
                                        <Col xs='5' sm='5' md='5' lg='5' xl='5' style={{ display: 'flex', alignItems: 'center' }}>
                                            <Label style={{ margin: 0 }}>
                                                <h6>
                                                    Consent Template :
                                                </h6>
                                            </Label>
                                        </Col>
                                        <Col xs='7' sm='7' md='7' lg='7' xl='7'>
                                            {(selectedCT === null) && (
                                                <UncontrolledDropdown>
                                                    <DropdownToggle outline caret style={{ width: '100%' }}>
                                                        Select Template
                                                    </DropdownToggle>
                                                    <DropdownMenu style={{ width: '100%' }}>
                                                        {consentTemplates.map((el) => (
                                                            <DropdownItem onClick={() => { onSelectCT(el); }}>{el.name}</DropdownItem>
                                                            // console.log(el,'el')
                                                        ))}
                                                    </DropdownMenu>
                                                </UncontrolledDropdown>
                                            )}
                                            {(selectedCT !== null) && (
                                                <div style={{ display: 'flex', alignItems: "center" }}>
                                                    <div style={{ display: 'inline-block', width: '80%' }}>
                                                        <label><p>{selectedCT['name']}</p></label>
                                                    </div>
                                                    <div style={{ display: 'inline-block', width: '20%' }}>
                                                        <Button onClick={() => removeSelectCT()} style={{ boxShadow: 'none', backgroundColor: 'transparent', borderColor: 'transparent', display: 'flex', alignItems: 'center' }}>
                                                            <span class="material-symbols-outlined" style={{ fontSize: '1.2em', color: 'red' }}>
                                                                close
                                                            </span>
                                                        </Button>
                                                    </div>
                                                </div>
                                            )}
                                        </Col>
                                    </Row>
                                </div>
                            </Col>
                        </Row>
                    </Container>
                    {/* {(RBACRoles.length > 0) && ( */}
                    <Row className='pt-4' style={{ width: '100%', justifyContent: 'center', margin: 0 }}>
                        <Col xs='11' sm='11' md='10' lg='10' xl='10' >
                            <Button size='lg' style={{ width: '100%', backgroundColor: "#3c6970" }} onClick={() => onAddApplication()}><h6>CREATE</h6></Button>
                            {/* onClick={() => onAddRBACRule()} */}
                        </Col>
                    </Row>
                    {/* )} */}
                </Container>
            )}
        </Container>
    );
}


export default NewAppConsent