import React, { useState, useEffect, useRef } from 'react';
import { useHistory } from "react-router-dom";
import Loader from '../../../Components/Loader';
import {
    Row, Col, Container, Button,
    Modal, ModalHeader, ModalBody, ModalFooter,
    Input, FormGroup, Label, Form, UncontrolledDropdown, DropdownToggle, DropdownMenu, DropdownItem
} from 'reactstrap';
const NewConsentTemplate = (props) => {
    const inputRef = useRef(null);
    const history = useHistory();
    const [error, setError] = useState(null)
    const [loading, setIsLoading] = useState(true)
    const [selectedMethod, setSelectedMethod] = useState(null)
    const [selectedDataPolicy, setSelectedDataPolicy] = useState(null)
    const [RBACRoles, setRBACRoles] = useState([])
    const [otherMeth, setOtherMeth] = useState([])
    const [dataItems, setDataItems] = useState([])
    const [, updateState] = React.useState();
    const [modalIsOpen, setModalIsOpen] = useState(false);
    const [templateName, setTemplateName] = useState('')
    const [NewDataFieldName, setNewDataFieldName] = useState('');
    const [NewPropose, setNewPropose] = useState('');
    const [rules, setRules] = useState([])
    const [isRequire, setIsRequire] = useState(null)
    const [disabledPage, setDisabledPage] = useState(false)

    useEffect(() => {
        // console.log(props.match.params.uid, 'uid is here')
        const abortCont = new AbortController();
        fetch("/pdpa/admin/getSecurityRules", { signal: abortCont.signal }).then(
            res => res.json()
        ).then(
            data => {
                let docKeys = Object.keys(data.data)
                let arrayOfDataRules = Object.values(data.data)
                let formattedDataRulesArray = []
                arrayOfDataRules.map((item, index) => {
                    formattedDataRulesArray.push({ ...item, 'docID': docKeys[index] })
                })
                // console.log(formattedDataRulesArray,'formattedDataRulesArray')
                if (formattedDataRulesArray.length === 0) {
                    setDisabledPage(true)
                }
                setRules(formattedDataRulesArray)
                // console.log(formattedDataRulesArray, 'formattedDataRulesArray')
                setIsLoading(false)
                window.scrollTo(0, 0);
            }
        ).catch(err => {
            if (err.name === 'AbortError') {
                console.log('fetch aborted')
            } else {
                setIsLoading(false);
                setError(err.message);

            }
        })
        return () => abortCont.abort();
    }, [])

    const forceUpdate = React.useCallback(() => updateState({}), []);

    const toggle = () => {
        let newToggleState = !modalIsOpen
        setModalIsOpen(newToggleState);
        if (newToggleState === false) {
            setNewDataFieldName('')
            setNewPropose('')
            setSelectedDataPolicy(null)
        }

    }
    const handleOpen = () => inputRef.current.focus();

    const changeNewDataFieldName = (e) => { setNewDataFieldName(e.target.value); }
    const changeNewPropose = (e) => { setNewPropose(e.target.value); }

    const addDataItem = () => {
        if (NewDataFieldName === '') {
            alert('Please fill the name of collected data field.')
            return
        } else if (NewPropose === '') {
            alert('Please fill the objectives of the data usage')
            return
        } else if (selectedDataPolicy === null) {
            alert('Please select security rule')
            return
        } else if (isRequire === null) {
            alert('Please identify if data is always required to be collected')
            return
        }
        else {
            let newDataItem = { 'dataFieldName': NewDataFieldName, 'purpose': NewPropose, 'secRule': selectedDataPolicy, 'isRequire': isRequire }
            let updatedDataItems = dataItems
            updatedDataItems.push(newDataItem)
            setDataItems(updatedDataItems)
            setNewDataFieldName('')
            setNewPropose('')
            setSelectedDataPolicy(null)
            setIsRequire(null)
            setModalIsOpen(false)
        }
    }

    const changeTemplateName = (e) => { setTemplateName(e.target.value); }

    const selectDataRule = (policy) => { setSelectedDataPolicy(policy) }
    const selectIsRequire = (val) => { setIsRequire(val) }

    const onCreateTemplate = () => {
        if (dataItems.length === 0 || templateName.replaceAll(' ', '') === '') {
            return
        }
        let formattedDTItems = []
        let requestPayload = {}
        requestPayload['name'] = templateName

        for (let n = 0; n < dataItems.length; n++) {
            // console.log(dataItems[n])
            let dtSecRule = dataItems[n]['secRule']['docID']
            formattedDTItems.push({ 'secRuleID': dtSecRule, 'dataField': dataItems[n]['dataFieldName'], 'purpose': dataItems[n]['purpose'], 'isRequire': dataItems[n]['isRequire'] === 'Yes (Mandatory)' ? true : false })
        }
        requestPayload['data'] = formattedDTItems
        requestPayload['modified_time'] = Math.floor(Date.now())

        let url = '/pdpa/admin/createConsentTemplate'
        let body = requestPayload
        // console.log(body, 'BD')

        fetch(url, {
            method: 'POST',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify(body)
        })
            .then((res) => res.json())
            .then((data) => {
                if (data['err']) {
                    alert(data['err'])
                    return
                }
                alert('The template is successfully created')
                history.push("/pdpa/auth/consent-template")

                // window.location.reload()

            }).catch((err) => alert('Unexpected error occurred, Please try again later'))
    }

    const delDataItem = (item_idx) => {
        let confirmDel = window.confirm('Do you confirm to delete this item?');
        if (confirmDel) {
            let updatedDataList = dataItems
            updatedDataList.splice(item_idx, 1);
            setDataItems(updatedDataList)
            forceUpdate()
        } else {
            return
        }
    }

    if(loading){
        return(<Loader/>)
      }

    return (
        <Container fluid style={{ minHeight: '86vh', width: '100vw', justifyContent: 'center' }}>
            <header className='pt-2'>
                <Container className='pt-2' style={{ width: "max(50%,320px)" }}>
                    <Container className='pt-1 pb-4' sm='10' md='8' lg='6' xl='6' >
                        <Row className='pt-4 pb-4' style={{ paddingLeft: '5%', paddingRight: '5%' }}>
                            <h2 style={{ textAlign: 'center' }}>Consent Template</h2>
                        </Row>
                    </Container>
                </Container>
            </header>
            <Container style={{ width: "max(50%,320px)" }}>
                {(disabledPage) && (
                    <Container className='pt-4 pb-4' sm='10' md='8' lg='6' xl='6' >
                        <h4 style={{textAlign:'center', color:"#a15255"}}>Please add a security policy before create new consent template</h4>
                    </Container>
                )}
                {(!disabledPage) && (
                    <Container className='pt-0 pb-4' sm='10' md='8' lg='6' xl='6' >
                        <Row className='pt-0 pb-3' style={{ width: '100%', justifyContent: 'center', margin: 0 }}>
                            <Col xs='12' sm='12' md='10' lg='10' xl='10'>

                                <Modal isOpen={modalIsOpen} toggle={toggle} onOpened={handleOpen}>
                                    <ModalHeader toggle={toggle}>Add Data Item</ModalHeader>
                                    <ModalBody>
                                        <FormGroup>
                                            <Label>Name of Collected Data Field:</Label>
                                            <Input innerRef={inputRef} type="text" name="text" placeholder="e.g., Full Name" onChange={changeNewDataFieldName} value={NewDataFieldName} />
                                        </FormGroup>
                                        <FormGroup>
                                            <Label>Propose:</Label>
                                            <Input type="text" name="text" onChange={changeNewPropose} value={NewPropose} />
                                        </FormGroup>
                                        <FormGroup>
                                            <Row>
                                                <Col xs='9' sm='9' md='9' lg='9' xl='9'>
                                                    <Label>Security Rule : <span style={{ color: '#8a2e2e' }}>{selectedDataPolicy !== null ? selectedDataPolicy['method'] + ' - ' + selectedDataPolicy['name'] : 'Not selected'}</span></Label>
                                                </Col>
                                                <Col xs='3' sm='3' md='3' lg='3' xl='3'>
                                                    <UncontrolledDropdown style={{ width: '100%', display: 'flex', justifyContent: 'flex-end' }}>
                                                        <DropdownToggle caret outline>
                                                            select
                                                        </DropdownToggle>
                                                        <DropdownMenu>
                                                            {rules.map((el, index) => (
                                                                <DropdownItem onClick={() => { selectDataRule(el); }}>{el.method} - {el.name}</DropdownItem>
                                                            ))}
                                                        </DropdownMenu>
                                                    </UncontrolledDropdown>
                                                </Col>
                                            </Row>
                                        </FormGroup>
                                        <FormGroup>
                                            <Row>
                                                <Col xs='9' sm='9' md='9' lg='9' xl='9'>
                                                    <Label>Is Always Required ?: <span style={{ color: '#8a2e2e' }}>{isRequire !== null ? isRequire : 'Not selected'}</span></Label>
                                                </Col>
                                                <Col xs='3' sm='3' md='3' lg='3' xl='3'>
                                                    <UncontrolledDropdown style={{ width: '100%', display: 'flex', justifyContent: 'flex-end' }}>
                                                        <DropdownToggle caret outline>
                                                            select
                                                        </DropdownToggle>
                                                        <DropdownMenu>
                                                            {['Yes (Mandatory)', 'No (Optional)'].map((el, index) => (
                                                                <DropdownItem onClick={() => { selectIsRequire(el); }}>{el}</DropdownItem>
                                                            ))}
                                                        </DropdownMenu>
                                                    </UncontrolledDropdown>
                                                </Col>
                                            </Row>
                                        </FormGroup>


                                    </ModalBody>
                                    <ModalFooter>
                                        <Button color='primary' onClick={() => addDataItem()}>Add</Button>
                                        <Button onClick={toggle}>Close</Button>
                                    </ModalFooter>
                                </Modal>
                            </Col>
                            <Col className='mt-0' xs='12' sm='12' md='10' lg='10' xl='10' >
                                <div className='mb-3' style={{ display: 'flex', alignItems: "center" }}>
                                    <div style={{ display: 'inline-block', width: '100%' }}>
                                        <Input type="text" name="text" id="exampleEmail" onChange={changeTemplateName} placeholder="Input template name" value={templateName} />
                                    </div>
                                    {/* {(dataItems.length !== 0) && ( */}
                                    <Row>
                                        {/* <Col>
                                        <Button onClick={() => { history.push("/pdpa/auth/consent-template") }} color='secondary' style={{ width: '100%', boxShadow: 'none', display: 'inline', display: 'flex', justifyContent: "center", alignItems: 'center' }}>
                                            <h5 style={{ margin: 0, marginRight: '3%' }}>Cancel</h5>
                                        </Button>
                                    </Col> */}
                                        <Col>
                                            <Button disabled={(dataItems.length === 0 || templateName.replaceAll(' ', '') === '') ? true : false} onClick={() => onCreateTemplate()} color='primary' style={{ width: '100%', boxShadow: 'none', display: 'inline', display: 'flex', justifyContent: "center", alignItems: 'center' }}>
                                                <h5 style={{ margin: 0, marginRight: '3%' }}>Create</h5>
                                            </Button>
                                        </Col>
                                    </Row>
                                    {/* )} */}
                                    {/* <div style={{ display: 'inline-block', width: '20%' }}>
                                    <Button style={{ boxShadow: 'none', display: 'inline', backgroundColor: 'transparent', borderColor: 'transparent', color: "black" }}>
                                        <span class="material-symbols-outlined" style={{ fontSize: '1.87em' }}>
                                            add_circle
                                        </span>
                                    </Button>
                                </div> */}
                                </div>
                                <div>
                                    {dataItems.map((item, idx) => (
                                        <div className='mb-2' style={{ borderRadius: '5px', backgroundColor: '#eae9e5', padding: '4% 5% 4% 5%', display: 'flex', alignItems: "center" }}>
                                            <div style={{ display: 'inline-block', width: '85%' }}>
                                                <h6 style={{ fontWeight: '600' }}>Name: {item['dataFieldName']}</h6>
                                                <p>Purpose: {item['purpose']}</p>
                                                <p>Security Rule: {item['secRule']['method']}{' - '}{item['secRule']['name']}</p>
                                                <p>Is Require: {item['isRequire']}</p>
                                            </div>
                                            <div style={{ display: 'inline-block', width: '15%', height: 'auto' }}>
                                                <Button onClick={() => delDataItem(idx)} style={{ boxShadow: 'none', display: 'inline', backgroundColor: 'transparent', borderColor: 'transparent', color: "black" }}>
                                                    <span class="material-symbols-outlined" style={{ fontSize: '2em', color: 'gray' }}>
                                                        delete
                                                    </span>
                                                </Button>
                                            </div>
                                        </div>
                                    ))}
                                </div>
                                <div className='pt-3'>
                                    {(dataItems.length === 0) && (
                                        <p className='mb-3' styles={{ textAlign: 'center' }}><i>Please Add Items</i></p>
                                    )}
                                    <Button outline color='primary' size='lg' style={{ width: '100%', boxShadow: 'none', display: 'inline', display: 'flex', justifyContent: "center", alignItems: 'center' }} onClick={() => { setModalIsOpen(true);}}>
                                        <h6 style={{ margin: 0, marginRight: '3%' }}>Add Data Item</h6>

                                        <span class="material-symbols-outlined" style={{ fontSize: '1.87em' }}>
                                            add_circle
                                        </span>
                                    </Button>
                                </div>
                            </Col>
                        </Row>
                    </Container>
                )}

            </Container>
        </Container>
    );
}

export default NewConsentTemplate