import { Link } from "react-router-dom";
import { Button } from 'semantic-ui-react'

const Navbar = () => {
  const logout = () => {
    window.localStorage.removeItem('token');
    window.localStorage.removeItem('refresh_token');
    window.localStorage.removeItem('exp_datetime');
    window.localStorage.removeItem('uid');
    window.localStorage.removeItem('email');

    window.location.reload()
  }
  return (
    <nav className="navbar pt-3 pb-4" style={{ backgroundColor: '#919f88', width: '100vw', display: 'flex', justifyContent: "center", alignItems: 'center' }}>
      {/* <h5>Home</h5> */}
      <div className="links" style={{marginRight:'14%'}}>
        <Link to={"/pdpa/auth/main"}>
          <span class="material-symbols-outlined" style={{ color: 'white', fontSize: '2.3em' }}>
            home
          </span>
        </Link>
        </div>
        <div className="links" style={{marginRight:'14%'}}>
        <Link to={"/pdpa/auth/main"}>
          <span class="material-symbols-outlined" style={{ color: 'white', fontSize: '2.3em' }}>
            notifications
          </span>
        </Link>
        </div>
        <div className="links">
        <Link onClick={() => logout()}>
          <span class="material-symbols-outlined" style={{ color: 'white', fontSize: '2.3em' }}>
            logout
          </span>
        </Link>
        </div>
        {/* <Button style={{backgroundColor:'transparent'}} onClick={() => logout()}>
          <span class="material-symbols-outlined" style={{color:'white', fontSize:'2.2em'}}>
            home
          </span></Button> */}
      
    </nav>
  );
}

export default Navbar;