import React, { useState, useEffect } from 'react';
import { Row, Col, Container, Button } from 'reactstrap';
import { Input } from 'semantic-ui-react'
import Loader from '../../../Components/Loader';
import { Link } from "react-router-dom";
const ViewAllPolicies = (props) => {

  // const [data, setData] = useState([{}])
  const [error, setError] = useState(null)
  const [loading, setIsLoading] = useState(true)
  const [rules, setRules] = useState({})

  useEffect(() => {
    // console.log(props.match.params.uid, 'uid is here')
    const abortCont = new AbortController();
    fetch("/pdpa/admin/getSecurityRules", { signal: abortCont.signal }).then(
      res => res.json()
    ).then(
      data => {
        // console.log(data, 'data from fb')
        setRules(data.data)
        setIsLoading(false)
        window.scrollTo(0, 0);
      }
    ).catch(err => {
      if (err.name === 'AbortError') {
        console.log('fetch aborted')
      } else {
        setIsLoading(false);
        setError(err.message);

      }
    })
    return () => abortCont.abort();
  }, [])

  const delRule = (docID) => {
    let confirmDel = window.confirm('All the data protected by this security rule will not be protected. Are you sure that you want to delete this security rule?');
    if (confirmDel) {
      let body = { 'docID': docID }
      fetch(`/pdpa/admin/deleteSecRule`, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(body)
      })
        .then((res) => res.json())
        .then((data) => {
          if (data['err']) {
            alert(data['err'])
            return
          }
          window.location.reload()
        }).catch((err) => alert('Unexpected error occurred, Please try again later'))
    } else {
      return
    }
  }

  let rulesUI = []
  if (rules !== {}) {
    let RBACroleString = ''
    let RBACotherString = ''
    let counter = 0
    let rulesDocKeys = Object.keys(rules)
    let objID = null
    for (const k in rules) {
      RBACroleString = ''
      RBACotherString = ''
      objID = rulesDocKeys[counter]
      counter += 1
      if (rules[k].method === 'RBAC') {
        rules[k].roles.map((val, i) => {
          if (i === 0) {
            RBACroleString = RBACroleString + val
          } else {
            RBACroleString = RBACroleString + ', ' + val
          }

        })
        if (rules[k].others.length === 0) {
          RBACotherString = '-'
        } else {
          rules[k].others.map((op, i) => {
            if (i === 0) {
              RBACotherString = RBACotherString + op
            } else {
              RBACotherString = RBACotherString + ', ' + op
            }
          })
        }
      }
      rulesUI.push(
        <Col className='pt-1 pb-2' xs='12' sm='12' md='10' lg='10' xl='10' key={k}>

          {/* <Link to={"/pdpa/auth/security-policy/add-rule"}> */}
          <Button outline size='lg' style={{ width: '100%', height: 'auto',  minHeight:'auto', overflowY:'hidden', overflowX:'hidden', paddingRight: "1%" }}>
            <Row>
              <Col xs='2' sm='2' md='2' lg='2' xl='2' style={{ display: 'flex', alignItems: "center" }}>
                <span style={{fontWeight:500, fontSize:"0.8em", textAlign:'center', whiteSpace:'nowrap'}}>RULE {counter}</span>
              </Col>
              <Col className='pt-2 pb-2'  xs='8' sm='8' md='8' lg='8' xl='8' >
              <p style={{ textAlign: 'left', paddingLeft: '8%', whiteSpace: "nowrap", fontWeight:600 }}>{rules[k].name}</p>
                <p style={{ textAlign: 'left', paddingLeft: '8%', whiteSpace: "nowrap" }}>Access Control: {rules[k].method}</p>
                {(rules[k].method === 'RBAC') && (
                  <p style={{ textAlign: 'left', paddingLeft: '8%' }}>Roles: {RBACroleString}</p>
                )}
                {(rules[k].method === 'RBAC') && (
                  <p style={{ textAlign: 'left', paddingLeft: '8%' }}>Others: {RBACotherString}</p>
                )}
              </Col>
              <Col xs='1' sm='1' md='1' lg='1' xl='1' onClick={() => delRule(objID)} style={{ display: "flex", justifyContent: 'center', alignItems: 'center' }} >
                <span class="material-symbols-outlined" style={{ fontSize: "1.5em" }}>
                  delete
                </span>
              </Col>

            </Row>
            {/* <div style={{ position: 'relative', top: 0, left: 0, width:'5%', height:'auto' }}>
              <span class="material-symbols-outlined" style={{fontSize:"1.5em"}}>
                delete
              </span>
            </div> */}
          </Button>
          {/* </Link> */}
        </Col>
      )
    }
  }

  if(loading){
    return(<Loader/>)
  }

  return (
    <Container fluid style={{ minHeight: '86vh', width: '100vw', justifyContent: 'center' }}>
      <header className='pt-2'>
        <Container className='pt-2' style={{ width: "max(50%,320px)" }}>
          <Container className='pt-1 pb-4' sm='10' md='8' lg='6' xl='6' >
            <Row className='pt-4 pb-4' style={{ paddingLeft: '5%', paddingRight: '5%' }}>
              {/* <h2 style={{ textAlign: 'center' }}>Security Policy</h2> */}
              <h3 style={{ textAlign: 'center', fontWeight:550}}>SECURITY POLICY</h3>
            </Row>
          </Container>
        </Container>
      </header>
      <Container style={{ width: "max(50%,320px)" }}>
        <Container className='pt-1 pb-4' sm='10' md='8' lg='6' xl='6' >
          <Row className='pt-1 pb-3' style={{ width: '100%', justifyContent: 'center', margin: 0 }}>
            <Col xs='12' sm='12' md='10' lg='10' xl='10' >
              <Link to={"/pdpa/auth/security-policy/add-rule"}>
                <Button className='shadow'  color='secondary' size='lg' style={{ width: '100%', height: 'max(12vh,65px)', backgroundColor:'#539dae', borderColor:"transparent"}}>
                  <Row>
                    <Col xs='9' sm='9' md='9' lg='9' xl='9' style={{ display: 'flex', justifyContent: 'cent', alignItems: 'center' }}>
                      <h4 style={{ textAlign: 'left', paddingLeft: '8%' }}>Add New Rule</h4>
                    </Col>
                    <Col xs='3' sm='3' md='3' lg='3' xl='3'>
                      <span class="material-symbols-outlined" style={{ fontSize: '1.9em' }}>
                        add_circle
                      </span>
                    </Col>
                  </Row>
                </Button>
              </Link>
            </Col>
          </Row>
          <Row className='pt-1 pb-3' style={{ width: '100%', justifyContent: 'center', margin: 0 }}>
            {rulesUI}
          </Row>











          {/* <Row className='pt-1 pb-3' style={{ width: '100%', justifyContent: 'center', margin: 0 }}>
            <Col xs='12' sm='12' md='10' lg='10' xl='10' >
              <Button outline size='lg' style={{ width: '100%',  height:'max(12vh,65px)'}}>
                <Row>
                  <Col xs='9' sm='9' md='9' lg='9' xl='9' style={{display:'flex', justifyContent:'cent', alignItems:'center'}}>
                    <h4 style={{ textAlign: 'left', paddingLeft: '8%' }}>Consent Template</h4>
                  </Col>
                  <Col xs='3' sm='3' md='3' lg='3' xl='3'>
                    <span class="material-symbols-outlined" style={{fontSize:'1.9em'}}>
                      edit_document
                    </span>
                  </Col>
                </Row>
              </Button>
            </Col>
          </Row> */}
          {/* <Row className='pt-1 pb-3' style={{ width: '100%', justifyContent: 'center', margin: 0 }}>
            <Col xs='12' sm='12' md='10' lg='10' xl='10' >
              <Button outline size='lg' style={{ width: '100%', height:'max(12vh,65px)'}}>
                <Row>
                  <Col xs='9' sm='9' md='9' lg='9' xl='9' style={{display:'flex', justifyContent:'cent', alignItems:'center'}}>
                    <h4 style={{ textAlign: 'left', paddingLeft: '8%' }}>Application Consent</h4>
                  </Col>
                  <Col xs='3' sm='3' md='3' lg='3' xl='3'>
                    <span class="material-symbols-outlined" style={{fontSize:'1.9em'}}>
                      widgets
                    </span>
                  </Col>
                </Row>
              </Button>
            </Col>
          </Row> */}
        </Container>
      </Container>
    </Container>
  );
}

export default ViewAllPolicies