import React, { useState, useEffect } from 'react';
import { Row, Col, Container, Button } from 'reactstrap';
import Loader from '../../../Components/Loader';
import { Input } from 'semantic-ui-react'
import { Link } from "react-router-dom";
const ViewAllConsentTemplate = (props) => {

  // const [data, setData] = useState([{}])
  const [error, setError] = useState(null)
  const [loading, setIsLoading] = useState(true)
  const [templates, setTemplates] = useState({})

  useEffect(() => {
    // console.log(props.match.params.uid, 'uid is here')
    const abortCont = new AbortController();
    fetch("/pdpa/admin/getConsentTemplate", { signal: abortCont.signal }).then(
      res => res.json()
    ).then(
      data => {
        // console.log(data.data, 'data from fb')
        setTemplates(data.data)
        setIsLoading(false)
        window.scrollTo(0, 0);
      }
    ).catch(err => {
      if (err.name === 'AbortError') {
        console.log('fetch aborted')
      } else {
        setIsLoading(false);
        setError(err.message);

      }
    })
    return () => abortCont.abort();
  }, [])

  const delTemplate = (docID) => {
    // console.log(docID,'del docID')
    let confirmDel = window.confirm('Do you confirm to delete this item?');
    if (confirmDel) {
      let body = { 'docID': docID }
      fetch(`/pdpa/admin/deleteTemplate`, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(body)
      })
        .then((res) => res.json())
        .then((data) => {
          if (data['err']) {
            alert(data['err'])
            return
          }
          window.location.reload()
        }).catch((err) => alert('Unexpected error occurred, Please try again later'))
    } else {
      return
    }
  }

  let templatesUI = []
  if (templates !== {}) {
    // let RBACroleString = ''
    // let RBACotherString = ''
    // let counter = 0
    let templatesDocKeys = Object.keys(templates)
    // console.log(templates, 'templates')
    let objID = null
    let template = null
    // console.log(templatesDocKeys, 'templatesDocKeys')
    for (let k in templatesDocKeys) {
      // console.log(k, 'k')
      template = templates[templatesDocKeys[k]]
      // console.log(template, 'template')
      templatesUI.push(
        <Col className='pt-1 pb-2' xs='12' sm='12' md='10' lg='10' xl='10' key={k}>
          <Button outline size='lg' style={{ width: '100%', height: 'auto', minHeight: '12vh', paddingRight: "1%" }}>
            <Row>
              <Col xs='3' sm='3' md='3' lg='3' xl='3' style={{ display: 'flex', alignItems: "center" }}>
                <h6>TP {parseInt(k) + 1}</h6>
              </Col>
              <Col xs='7' sm='7' md='7' lg='7' xl='7' style={{ display: 'flex', alignItems: "center", textAlign: 'left' }}>
                <h6 style={{fontWeight:'600'}}>{template.name}</h6>
              </Col>
              <Col xs='1' sm='1' md='1' lg='1' xl='1' style={{ display: "flex", justifyContent: 'center', alignItems: 'center' }} >
                <span class="material-symbols-outlined" onClick={() => delTemplate(templatesDocKeys[k])} style={{ fontSize: "1.3em" }}>
                  delete
                </span>
              </Col>
            </Row>
          </Button>
        </Col>
      )
    }
  }

  if(loading){
    return(<Loader/>)
  }

  return (
    <Container fluid style={{ minHeight: '86vh', width: '100vw', justifyContent: 'center' }}>
      <header className='pt-2'>
        <Container className='pt-2' style={{ width: "max(50%,320px)" }}>
          <Container className='pt-1 pb-4' sm='10' md='8' lg='6' xl='6' >
            <Row className='pt-4 pb-4' style={{ paddingLeft: '5%', paddingRight: '5%' }}>
              <h2 style={{ textAlign: 'center' }}>Consent Template</h2>
            </Row>
          </Container>
        </Container>
      </header>
      <Container style={{ width: "max(50%,320px)" }}>
        <Container className='pt-1 pb-4' sm='10' md='8' lg='6' xl='6' >
          <Row className='pt-1 pb-3' style={{ width: '100%', justifyContent: 'center', margin: 0 }}>
            <Col xs='12' sm='12' md='10' lg='10' xl='10' >
              <Link to={"/pdpa/auth/consent-template/create-template"}>
                <Button className='shadow'  color='secondary' size='lg' style={{ width: '100%', height: 'max(12vh,65px)', backgroundColor:'#dca491', borderColor:"transparent" }}>
                  <Row>
                    <Col xs='9' sm='9' md='9' lg='9' xl='9' style={{ display: 'flex', justifyContent: 'cent', alignItems: 'center' }}>
                      <h4 style={{ textAlign: 'left', paddingLeft: '8%' }}>Create Template</h4>
                    </Col>
                    <Col xs='3' sm='3' md='3' lg='3' xl='3'>
                      <span class="material-symbols-outlined" style={{ fontSize: '1.9em' }}>
                        add_circle
                      </span>
                    </Col>
                  </Row>
                </Button>
              </Link>
            </Col>
          </Row>
          <Row className='pt-1 pb-3' style={{ width: '100%', justifyContent: 'center', margin: 0 }}>
            {templatesUI}
          </Row>
        </Container>
      </Container>
    </Container>
  );
}

export default ViewAllConsentTemplate